import { useEffect, useState } from 'react'

const useAuctionData = () => {
    const [auctions, setAuctions] = useState([]);
    const [countdown, setCountdown] = useState({});
    const [lastUpdated, setLastUpdated] = useState(Date.now());
    const [currentAuction, setCurrentAuction] = useState({});

    const _getAuctions = () => {
        return new Promise((resolve, reject) => {
            fetch(`auction/auctions`).then(result => {
                result.json().then(res => {
                    setAuctions(res)
                    resolve();
                })
            }).catch(err => reject(err))
        })
    }

    const _getCurrentAuction = () => {
        return new Promise((resolve, reject) => {
            fetch(`auction/current`).then(result => {
                result.json().then(res => {
                    setCurrentAuction(res)
                    resolve();
                })
            }).catch(err => reject(err))
        })
    }

    const _getCountdown = () => {
        return new Promise((resolve, reject) => {
            fetch(`auction/countdown`).then(result => {
                result.json().then(res => {
                    setCountdown(res)
                    resolve();
                })
            }).catch(err => reject(err))
        })
    }

    useEffect(() => {
        _getAuctions();
        _getCountdown();
        _getCurrentAuction();
        const UPDATER = setInterval(() => _getCurrentAuction(), (1000 * 60) * 5)
        
        return (() => clearInterval(UPDATER))
    // eslint-disable-next-line
    }, [])

    return {
        auctions,
        countdown,
        lastUpdated,
        currentAuction
    }
}

export default useAuctionData;